.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.input_login {
  display: block;
  padding-left: 10px;
  width: 50%;
  height: 50px;
  margin: 10px auto;
  border: 1px solid #cccccc;
  border-radius: 5px;
  font-size: 15px;
  background-color: #f2f2f2;
}
.input_date {
  display: block;
  padding-left: 10px;
  width: 50%;
  height: 35px;
  margin: 10px auto;
  border: 1px solid #cccccc;
  border-radius: 5px;
  font-size: 15px;
  background-color: #f2f2f2;
}
.input_modal {
  display: block;
  padding-left: 10px;
  width: 91%;
  height: 30px;
  border: 1px solid #cccccc;
  border-radius: 5px;
  font-size: 15px;
  margin-left: 10px;
}

.checkbox {
  width: 20%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}
input[type="submit"] {
  background-color: #4caf50;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type="submit"]:hover {
  background-color: #45a049;
}

.container_1 {
  border-radius: 5px;
  background-color: lightgrey;
  padding: 15px;

  width: 35%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.container_login {
  border-radius: 5px;
  padding: 20px;
  position: fixed;
  left: 50%;
  top: 50%;
  width: 40%;
  transform: translate(-50%, -50%);
}
.check_box {
  padding: 5px;
}
.availablity {
  padding-bottom: 30px;
}
.button-primary {
  display: block;
  width: 53%;
  height: 40px;
  margin: 10px auto;
  font-size: 12px;
  color: white;
  background: cornflowerblue;
}
.button-secondary {
  display: block;
  width: 53%;
  height: 50px;
  margin: 10px auto;
  font-size: 12px;
  color: white;
  background: cornflowerblue;
  cursor: pointer;
}
.button-submit {
  height: 36px;
  font-size: 12px;
  color: white;
  background: cornflowerblue;
  cursor: pointer;
  border-radius: 7px;
}
.pt2 {
  padding-top: 20px;
}
.login {
  font-size: 25px;
  font-weight: bolder;
  color: cornflowerblue;
}
.header-main {
  height: 50px;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  background: #2e3f51;
  font-family: fantasy;
  border-left: 1px whiter bold;
}
.main_container {
  display: flex;
}
.sidebar {
  flex: 1;
  height: 100vh;
  background: #2e3f51;
  color: white;
}
.body-content {
  flex: 10;
  height: 100vh;
}
.header {
  color: white;
  font-size: 36px;
}
