.options {
  padding-top: 10px;
}
.pt2 {
  padding-top: 20px;
}
.pt1 {
  padding-top: 10px;
}
.tabs {
  background: #09d3ac;
}
.tab_1 {
  display: flex !important;
  justify-content: center;
}
.tab_2 {
  display: flex !important;
  justify-content: center;
  flex-direction: column
}
table {
  width: 500px;
  margin: 0 auto;
  border-collapse: collapse;
}

table, th, td {
  border: 1px solid black;
}
.tab_modal {
  display: flex;
  justify-content: space-between;
}
.modal_button {
  display: flex;
  justify-content: flex-end;
}
.add_button {
  display: flex;
  justify-content: center;
}
button:disabled{
  background: #cccccc;
}